// Галлерея в которую рендерим фотки
.gallery {
  display: flex;
  flex-wrap: wrap;

  justify-content: center;

  padding-left: 15px;
  padding-right: 15px;

  margin-left: -15px;
  margin-top: -15px;
}

// Оформление одной карточки
.photo-card {
  flex-basis: calc(100% / 4 - 15px);

  margin-left: 15px;
  margin-top: 15px;
}

.photo-card:hover {
  transform: scale(1.03);
  cursor: pointer;
}

.gallery-img {
  display: block;
  width: 320px;
  height: 240px;

  object-fit: fit;
}

.photo-card > .info {
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

.info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  padding: 15px;
}

.info-item {
  margin: 0;

  text-align: center;

  font-size: 15px;
}

